@import url(https://fonts.googleapis.com/css?family=Gilda+Display);

.App {
  font-family: monospace;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;  
  background-color: #3a86ff;
  opacity: 0.8;
  background: linear-gradient(135deg, #ffbe0b55 25%, transparent 25%) -10px 0/ 20px 20px, linear-gradient(225deg, #ffbe0b 25%, transparent 25%) -10px 0/ 20px 20px, linear-gradient(315deg, #ffbe0b55 25%, transparent 25%) 0px 0/ 20px 20px, linear-gradient(45deg, #ffbe0b 25%, #3a86ff 25%) 0px 0/ 20px 20px;


}


.Joke {
    display: flex;
    border-bottom: 1px solid black;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    font-family: monospace;
    padding: 1rem;
}



.Joke:hover {
   background-color: rgba(128, 128, 128, 0.233);
   box-shadow: 5px 5px 15px 5px #000000;
}


.Joke-buttons {
    display: flex;
    margin-right: 1rem;
    margin-left: 1rem;
    align-items: center;
    justify-content: center;
    width: 30%;
    font-family: monospace;
}

.Joke-text{
    width: 80%;
    font-size: 1rem;
}



#Joke-votes{
    text-align: center;
    font-size: 15px;
    font-weight: 800;
    width: 50px;
    height: 50px;
    margin-top: 5px;
    padding-top: 5px;
    text-align: center;
    align-items: center;
    align-self: center;
    justify-content: center;
}

.fa-thumbs-up, .fa-thumbs-down{
    font-size: 2em;
    margin: 10px;
    cursor: pointer;
    transition: all 0.2s;
}    

.fa-thumbs-up:hover {
    color: #6a994e;
    -webkit-animation-name: move-up;
            animation-name: move-up;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

.fa-thumbs-down:hover{
    color: #bc4749;
    -webkit-animation-name: move-down;
            animation-name: move-down;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    
}

@-webkit-keyframes move-up {
    0%,
    50%,
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    25%,
    75% {
        -webkit-transform: translateY(-3px);
                transform: translateY(-3px);
    }
}

@keyframes move-up {
    0%,
    50%,
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    25%,
    75% {
        -webkit-transform: translateY(-3px);
                transform: translateY(-3px);
    }
}

@-webkit-keyframes move-down {
    0%,
    50%,
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    25%,
    75% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}

@keyframes move-down {
    0%,
    50%,
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    25%,
    75% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}

@media only screen and (max-width: 750px) {
    .Joke {
      flex-direction: column;
      text-align: center;

    }
    .Joke-buttons {
        font-size: 10px;
    }
  }
.JokeList{
    display: flex;
    width: 90%;
    height: 90vh;
    font-family: monospace;
    padding-top: 100px;

}

.JokeList-sidebar {
    font-family: monospace;
    background: #0a4991;
    border-radius: 10% 0% 0% 10% / 10% 8% 5% 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 35%;
    box-shadow: 5px 5px 15px 5px #0000006c;
    z-index: 2;
}

.JokeList-jokes {
    height: 100%;
    background-color: rgb(216, 216, 216);
    width: 70%;
    align-self: center;
    overflow: scroll;   
    box-shadow: 5px 5px 15px 5px #00000096;
    border-radius: 0% 10% 10% 0% / 0% 10% 10% 10% ;
 
}

.JokeList-title {
    font-size: 4rem;
    margin: 1rem;
    color: #ffbe0b;
    font-weight: 900;
    letter-spacing: 0.1rem;
    text-align: center;
    margin-bottom: 5px;
}

.JokeList-title span{
    margin: 0.2rem;
    color: #ffbe0b;
    font-weight: 50;
    font-style: italic;
    text-align: center;
    font-family: 'cursive';
}


.JokeList-getmore {
    font-size: 1rem;
    width: 50%;
    border-radius: 2rem;
    padding: 0.7rem 1.2rem ;
    transition: 1s cubic-bezier(0.2);
    border: 2px solid #fb5607;
    color: #fb5607;
    margin-bottom: 2em;
}

.JokeList-getmore:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.548);
    background-color: #ffbe0b ;
    cursor: pointer;
    -webkit-animation: shake 1s;
            animation: shake 1s;
}

.JokeList-emoji {
    font-size: 100px;
    margin: 0
}

.JokeList-shake:hover {
    -webkit-animation: shake 1s;
            animation: shake 1s;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(1.5px, 1.5px) rotate(0deg); transform: translate(1.5px, 1.5px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
  }

@keyframes shake {
    0% { -webkit-transform: translate(1.5px, 1.5px) rotate(0deg); transform: translate(1.5px, 1.5px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
  }

/* spinner animation */
html,
body {
  height: 100vh;
  margin: 0;
  width: 100vw;
}

.loading {
  background: #ffbe0b;
  height: 100%;
  position: relative;
  width: 100%;
  display: block;
  align-items: center;
  text-align: center;
}

.loading h1 {
    font-size: 50px;
}


.spinner {
  -webkit-animation-name: rotate;
          animation-name: rotate; 
  -webkit-animation-duration: 0.8s; 
          animation-duration: 0.8s; 
  -webkit-animation-iteration-count: infinite; 
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  position: absolute;
  left: calc(50% - 100px);
  top: calc(50% - 100px);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}


@-webkit-keyframes rotate {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  }


@keyframes rotate {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  }


   /* width */
::-webkit-scrollbar {
    width: 20px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #ffbe0b; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #fb5607; 
  }

